#tracker-view-cont {
	height: calc(100vh - 80px);
	min-height: calc(100vh - 80px);
	max-height: calc(100vh - 80px);
	width: 100%;
	display: grid;
	grid-template-columns: 70% 27.5%;
	gap: 1%;
}

.medium-cards>#tracker-view-cont {
	display: flex;
	flex-direction: column;
}

.medium-cards>#tracker-view-cont>#tracker-month-view {
	height: 66.5% !important;
	min-height: 66.5% !important;
	max-height: 66.5% !important;
}
.medium-cards>#tracker-view-cont>#lead-summary-card {
	height: 32.5%;
	min-height: 32.5% !important;
	max-height: 32.5% !important;
}
.list>#tracker-view-cont>#tracker-month-view {
	height: 100%;
	min-height: 100%;
	max-height: 100%;
}