#commissions-card {
	height: calc(100vh - 80px);
	min-height: calc(100vh - 80px);
	max-height: calc(100vh - 80px);
	min-width: calc(100% - 20px);
	max-width: calc(100% - 20px);
}
#commissions-card>.card-menubar {
	position: relative;
}
.medium-cards>#commissions-card {
	min-width: 100%;
	max-width: 100%;
}