.App {
  text-align: center;
}

.bg-light-gray {
	background-color: #f9f9f9;
}

.app-container {
	width: 100%;
	height: 100%;
	overflow-y: scroll;
	display: flex;
	flex-direction: column;
}
#main-app {
	position: absolute;
	top: 40px;
	height: calc(100% - 40px);
}
#main-app>:last-child {
	margin-bottom: 20px;
}
.app-container.medium-cards {
	display: grid;
	grid-template-columns: 49.5% 49.5%;
	gap: 1%;
	row-gap: 40px;
	height: calc(100vh - 60px);
	min-height: calc(100vh - 60px);
	max-height: calc(100vh - 60px);
}
.app-container.list {
	display: flex;
	flex-direction: column;
	gap: 5%;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.no-padding {
	padding: 0px;
}
.card.no-padding>.card-content {
	padding: 0px;
}

.outline-card {
	border: 1px solid #ccc;
	filter: unset;
}

.menu-bar-custom {
	background-color: #062a49;
	position: absolute;
	top:0px;
	width: 60px;
	height: 100vh;
}
