#lead-overview-card {
	min-height: 75vh;
	max-height: 75vh;
	height: 75vh;
	min-width: 90vw;
}
#lead-overview-card>.card-content {
	height: calc(100% - 40px);
	padding: 0px;
}
#lead-overview-card>.card-content>.action-row {
	border-top: 1px solid #ccc;
	align-items: center;
	justify-content: center;
	height: 10%;
}

#lead-overview-card>.card-content>.bid-cont {
	height: 40%;
	width: 100%;
	border-top: 1px solid #ccc;
}

#lead-overview-card>.card-content>#lead-form-cont {
	/*display: grid;
	grid-template-columns: repeat(3, 31.5%);*/
	display: flex;
	flex-direction: column;
	flex-wrap: wrap;
	gap: 2.5%;
	height: 50%;
	padding: 10px;
}
.bid-cont {
	padding: 0px !important;
}

#lead-overview-card>.card-content>#lead-form-cont>.form-info-item {
	display: grid;
	grid-template-columns: 50% 50%;
	height: 40px;
	border-radius: 5px;
	align-items: center;
	padding-left: 10px;
	padding-right: 10px;
	border: 1px solid transparent;
}
#lead-overview-card>.card-content>#lead-form-cont>.form-info-item:hover {
	background-color: #f0f0f0;
	border: 1px solid #cfcfcf;
}

#lead-overview-card>.card-content>#lead-form-cont>.form-info-item>.form-info-title {
	width: 100%;
	font-size: larger;
	gap: 0px;
}
#lead-overview-card>.card-content>#lead-form-cont>.form-info-item>.checkbox-cont {
	justify-self: end;
}
#lead-overview-card>.card-content>#lead-form-cont>.form-info-item>:nth-child(2) {
	width: 100%;
}

/*Bid table*/
#bid-table-header {
	margin: 0px;
	padding: 0px;
	background-color: #f0f0f0;
	align-items: center;
	justify-content: center;
	border-bottom: 1px solid #ccc;
	height: 40px;
}
#bid-table-header>div {
	background-color: transparent;
	border-right: 1px solid #ccc;
	margin: 0px;
	line-height: 40px;
	font-size: larger;
	font-weight: 600;
}

.bid-cont {
	display: flex;
	flex-direction: column;
}

.bid-table-cont {
	height: 80%;
}

.add-bid-row {
	height: 20%;
	align-items: center;
	padding: 10px;
	border-top: 1px solid #ccc;
	display: grid;
	grid-template-columns: repeat(6, 16%);
	align-items: center;
	justify-content: center;
}

.bid-table {
	height: calc(100% - 40px);
	overflow-y: scroll;
}
.bid-table>.tablerow {
	height: fit-content;
	margin: 0px;
	padding: 10px;
	border-bottom: 1px solid #ccc;
	align-items: center;
	justify-content: center;
}
.bid-table>.tablerow:hover {
	background-color: #fafafa;
}
.bid-table>.tablerow>div {
	margin: 0px;
	padding: 0px;
}

/*Action form customization*/
#bid-sold-form-item-cont {
	display: grid;
	grid-template-columns: 75% 25%;
}
#bid-sold-form-item-cont>.checkbox-cont {
	height: 25px;
	width: 25px;
}
#bid-cat-form-item-cont>.dropdown-cont>.dropdown-list {
	height: 170px;
}