#lead-summary-card>.card-menubar {
	position: relative;
}

#lead-summary-card>.card-content {
	display: flex;
	flex-direction: column;
	gap: 1%;
	height: calc(100% - 40px);
}
.medium-cards>#tracker-view-cont>#lead-summary-card>.card-content {
	flex-direction: row;
	width: 100%;
}
.medium-cards>#tracker-view-cont>#lead-summary-card>.card-content>.card {
	width: 50%;
	min-height: 100%;
	max-height: 100%;
}

#lead-summary-card>.card-content>.card {
	min-height: 49%;
	max-height: 49%;
}

#lead-summary-card>.card-content>.card>.card-menubar {
	height: 30px;
	line-height: 10px;
}

#lead-summary-card>.card-content {
	display: flex;
	flex-direction: column;
	align-content: center;
	justify-content: center
}
.lead-sum-form-item {
	width: 95%;
	display: grid;
	grid-template-columns: 45% 55%;
	align-self: center;
	padding-top:5px;
	padding-bottom:5px;
	gap: 0px;
	font-size: 1.5rem;
	padding-left: 10px;
	padding-right: 10px;
	border-radius: 10px;
	border: 1px solid transparent;
}
.lead-sum-form-item:hover {
	border: 1px solid #ccc;
	background-color: #e5e5e5;
}
.lead-sum-form-item>.form-info-title {
	font-weight: 600;
}
.lead-sum-form-item>div {
	font-style: italic;
	color: #333333;
	text-align: end;
}
.lead-sum-form-item#revenue-cont>div::before,
.lead-sum-form-item#rpo-cont>div::before {
	content: "$";
}
.lead-sum-form-item#closepercent-cont>div::after {
	content: "%";
}
/*Adjust lead summary form when viewed in medium cards*/
.medium-cards>#tracker-view-cont>#lead-summary-card>.card-content>.card>.card-content>.lead-sum-form-item {
	font-size: larger;
}
.medium-cards>#tracker-view-cont>#lead-summary-card>.card-content>.card>.card-content {
	display: grid;
	grid-template-columns: 50% 50%;
}